$(function () {
	let walkingPointsCollection,
	  parkingPointsCollection,
		walkingLinesCollection,
		parkingLinesCollection,
		searchBlock = $('.js-search-block'),
		searchField = $('.js-search-field'),
		searchButton = $('.js-search-button'),
		carAddress = 'Москва, Кремль',
		multiRoute;

let theatre = [56.166744, 37.537734];

// Черные точки пешеходного маршрута на карте
const walkingPointsCoords = [
			[56.176092, 37.476514],
			[56.166630, 37.537908],
			[56.166640, 37.537908]
];

const parkingPointsCoords = [
	[56.161525, 37.470598],
	[56.166620, 37.537908]
];

// Линии пешего маршрута
const walkingLinesCoords = [
			[[56.171780, 37.509419], [56.172989, 37.510438]],
			[[56.172989, 37.510438], [56.172756, 37.511662]],
			[[56.172756, 37.511662], [56.172852, 37.512348]],
			[[56.172852, 37.512348], [56.169078, 37.511435]],
			[[56.169078, 37.511435], [56.168984, 37.512412]],
			[[56.168984, 37.512412], [56.168683, 37.512482]],
			[[56.168683, 37.512482], [56.167483, 37.516418]],
			[[56.167483, 37.516418], [56.167799, 37.520863]],
			[[56.167799, 37.520863], [56.165185, 37.526340]],
			[[56.165185, 37.526340], [56.167152, 37.529840]],
			[[56.167152, 37.529840], [56.167027, 37.534502]]

];

// Линии маршрута
const parkingLinesCoords = [
	[[56.147160, 37.543117], [56.149809, 37.536139]],
	[[56.149809, 37.536139], [56.151058, 37.519883]],
	[[56.151058, 37.519883], [56.153296, 37.516878]],
	[[56.153296, 37.516878], [56.157858, 37.517054]],
	[[56.157858, 37.517054], [56.157858, 37.517054]],
	[[56.157858, 37.517054], [56.167799, 37.520863]],
	[[56.167799, 37.520863], [56.165185, 37.526340]],
	[[56.165185, 37.526340], [56.167152, 37.529840]],
	[[56.167152, 37.529840], [56.167027, 37.534502]]

];

try {
	ymaps.ready(function () {
		let myMap = new ymaps.Map('map', {
				center: [56.166744, 37.537734],
				zoom: 14,
				controls: []
			}, {
				searchControlProvider: 'yandex#search'
			}),

			myPlacemark = new ymaps.Placemark(myMap.getCenter(), {
				balloonContentBody: "Лисья Нора",
				hintContent: "Наш адрес"
			}, {
				iconLayout: 'default#image',
				//iconImageHref: '../images/check.png',
				iconImageSize: [44, 50],
				// Смещение левого верхнего угла иконки относительно
				// её "ножки" (точки привязки).
				iconImageOffset: [-22, -50],
			});

		myMap.geoObjects.add(myPlacemark);

		// --- Точки ---
		// --- Пешком ---
		walkingPointsCollection = new ymaps.GeoObjectCollection({}, {
			// Опции.
			// Необходимо указать данный тип макета.
			iconLayout: 'default#image',
			// Своё изображение иконки метки.
			//iconImageHref: '../images/check.png',
			// Размеры метки.
			iconImageSize: [10, 10],
			// Смещение левого верхнего угла иконки относительно
			// её "ножки" (точки привязки).
			iconImageOffset: [-5, -3]
			// preset: 'islands#circleIcon',
			// iconColor: '#DB2B27'
			// preset: 'islands#circleIcon',
			// iconColor: '#006DD2'
		});

		for (let i = 0; i < walkingPointsCoords.length; i++) {
			walkingPointsCollection.add(new ymaps.Placemark(walkingPointsCoords[i]));
		}
		// --- Парковка ---
		parkingPointsCollection = new ymaps.GeoObjectCollection({}, {
			iconLayout: 'default#image',
			//iconImageHref: '../images/check.png',
			iconImageSize: [10, 10],
			iconImageOffset: [-5, -3]
		});

		for (let i = 0; i < parkingPointsCoords.length; i++) {
			parkingPointsCollection.add(new ymaps.Placemark(parkingPointsCoords[i]));
		}
		// --- / Точки ---

		// --- Линии ---
		// --- Пешком ---
		walkingLinesCollection = new ymaps.GeoObjectCollection({}, {
			preset: 'islands#circleIcon',
			strokeColor: '#FF4D00',
			strokeWidth: 3
		});

		for (let i = 0; i < walkingLinesCoords.length; i++) {
			walkingLinesCollection.add(new ymaps.Polyline(walkingLinesCoords[i]));
		}

		// --- Парковка ---
		parkingLinesCollection = new ymaps.GeoObjectCollection({}, {
			preset: 'islands#circleIcon',
			strokeColor: '#FF4D00',
			strokeWidth: 3
		});

		for (let i = 0; i < parkingLinesCoords.length; i++) {
			parkingLinesCollection.add(new ymaps.Polyline(parkingLinesCoords[i]));
		}
		// --- / Линии ---

		myMap.geoObjects.add(walkingPointsCollection);
		myMap.geoObjects.add(walkingLinesCollection);
		myMap.setBounds(myMap.geoObjects.getBounds(), {checkZoomRange: true});
		//myMap.geoObjects.removeAll();
		myMap.geoObjects.add(myPlacemark);

		let $routeBtn = $('.js-route-btn');
		$routeBtn.click(function(e){
			e.preventDefault();
			let $this = $(this);
			let target = $this.attr('data-type');
			$routeBtn.removeClass('active');
			$this.addClass('active');

			if (target === 'walking') {
				createWalkingRoute();
			}
			else if (target === 'car') {
				searchBlock.addClass('is-active');
				myMap.setZoom(16);
				myMap.setCenter([56.165159, 37.526387], 12);
				myMap.geoObjects.removeAll();
				myMap.geoObjects.add(myPlacemark);
				searchField.on('keypress', function (e) {
					if (e.which === 13) {
						console.log('jkjgghgh');
						e.preventDefault();
						carAddress = searchField.val();
						createRouteByCar(carAddress);
						console.log('jkjgghgh');
					}
				});
				searchButton.on('click', function (e) {
					if (e) {
						e.preventDefault();
						carAddress = searchField.val();
						createRouteByCar(carAddress);
					}
				});
			}
			else if (target === 'parking') {
				createParkingRoute();
			}
			return false
		});

		// Создание пешего маршрута
		let createWalkingRoute = function() {
			searchBlock.removeClass('is-active');
			// myMap.setZoom(16);
			myMap.geoObjects.removeAll();
			myMap.geoObjects.add(myPlacemark);
			myMap.geoObjects.add(walkingPointsCollection);
			myMap.geoObjects.add(walkingLinesCollection);
			myMap.setBounds(myMap.geoObjects.getBounds(), {checkZoomRange: true});
		};

		// Создание автомобильного маршрута
		let createRouteByCar = function (carAddress) {
			myMap.geoObjects.removeAll();
			if (searchField.val() === '') {
				searchBlock.addClass('error');
				return false;
			} else {
				searchBlock.removeClass('error');
				multiRoute = new ymaps.multiRouter.MultiRoute({
					// Описание опорных точек мультимаршрута.
					referencePoints: [
						carAddress,
						[56.166744, 37.537734]
					],
					// Параметры маршрутизации.
					params: {
						routingMode: 'auto',
						// Ограничение на максимальное количество маршрутов, возвращаемое маршрутизатором.
						results: 1
					}
				}, {
					// Автоматически устанавливать границы карты так, чтобы маршрут был виден целиком.
					boundsAutoApply: true
				});

				// Добавляем мультимаршрут на карту.
				myMap.geoObjects.add(multiRoute);
			}
		};

		// Создание маршрута до пристани
		let createParkingRoute = function() {
			searchBlock.removeClass('is-active');
			myMap.geoObjects.removeAll();
			myMap.geoObjects.add(myPlacemark);
			myMap.geoObjects.add(parkingPointsCollection);
			myMap.geoObjects.add(parkingLinesCollection);
			myMap.setBounds(myMap.geoObjects.getBounds(), {checkZoomRange: true});
		};

	});

} catch (err) {

}

});
