$(function () {
	if ($('#guest').length) {
		const guestTemplate = document.querySelector('#guest') // Шаблон гостя
			.content
			.querySelector('.form-row--guest');
		const guestContainer = document.querySelector('.order-block__field-wrapper--guest'); //Куда вставляем поля гостя
		const addGuestBtn = document.querySelector('.js-guest-add'); // кнопка добавить гостя
		const removeGuestBtn = document.querySelector('.js-guest-remove'); // кнопка удалить гостя
		const countGuest = document.querySelector('.js-guest-count');


		const transportTemplate = document.querySelector('#transport') // Шаблон ТС
			.content
			.querySelector('.form-row--guest');
		const transportContainer = document.querySelector('.order-block__field-wrapper--transport');
		const addTransportBtn = document.querySelector('.js-transport-add'); // Кнопка добавить ТС
		const removeTransportBtn = document.querySelector('.js-transport-remove'); // Кнопка добавить ТС
		const countTransport = document.querySelector('.js-transport-count');

		let counterGuest = 0;
		let counterTransport = 0;

		removeGuestBtn.classList.add('not-active-order');
		removeTransportBtn.classList.add('not-active-order');

		countGuest.textContent = counterGuest;

		// Функция отрисовки
		let renderFields = function (container, template) {
			let tempElem = template.cloneNode(true);
			container.append(tempElem);
		};

		let renderGuest = function () {
			renderFields(guestContainer, guestTemplate);
			counterGuest++;
			countGuest.textContent = counterGuest;
			removeGuestBtn.classList.remove('not-active-order');
		};

		let renderTransport = function () {
			renderFields(transportContainer, transportTemplate);
			counterTransport++;
			countTransport.textContent = counterTransport;
			removeTransportBtn.classList.remove('not-active-order');
		}

		// При загрузке страницы вставляем 2 поля в каждую форму
		for (let i = 0; i < 2; i++) {
			renderGuest();
			renderTransport();
		};

		$('.js-select').select2({
			dropdownAutoWidth: true,
			containerCssClass: 'select',
			dropdownCssClass: 'no-input'
		});

		addGuestBtn.addEventListener('click', function () { // По клику на добавить вставка гостя
			renderGuest();
		});

		addTransportBtn.addEventListener('click', function () { // По клику на добавить вставка ТС
			renderTransport();
			$('.js-select').select2({
				dropdownAutoWidth: true,
				containerCssClass: 'select',
				dropdownCssClass: 'no-input'
			});
		});

		removeGuestBtn.addEventListener('click', function () {
			if (counterGuest !== 0) {
				guestContainer.lastElementChild.remove();
				counterGuest--;
				countGuest.textContent = counterGuest;
				if (counterGuest == 0) {
					removeGuestBtn.classList.add('not-active-order');
				}
			}
		});

		removeTransportBtn.addEventListener('click', function () {
			if (counterTransport !== 0) {
				transportContainer.lastElementChild.remove();
				counterTransport--;
				countTransport.textContent = counterTransport;
				if (counterTransport == 0) {
					removeTransportBtn.classList.add('not-active-order');
				}
			}
		});
	}
});
