$(function () {
	const buttons = document.querySelectorAll('.contacts-block__transport');
	const titles = document.querySelectorAll('.form-contacts__title');
	const texts = document.querySelectorAll('.form-contacts__text-wrapper');

	const mainWork = function(btn, title, text) {
		for (let i = 0; i < buttons.length; i++) {
			buttons[i].classList.remove('js-transport-active');
			titles[i].classList.remove('js-text-active');
			texts[i].classList.remove('js-text-active');
		}

		btn.classList.add('js-transport-active');
		title.classList.add('js-text-active');
		text.classList.add('js-text-active');
	};

	for (let i = 0; i < buttons.length; i++) {
		buttons[i].addEventListener('click', function() {
			mainWork(buttons[i], titles[i], texts[i]);
		});
	}

});
