$(function () {

// Ввод только цифр
	$('.js-number').keypress(function(e){
		let charCode = (e.which) ? e.which : e.keyCode;
		if (charCode > 31 && (charCode < 48 || charCode > 57)) {
		  return false;
		}
	});

// Деактивация кнопки отправки формы при снятии галочки с чекбокса согласия
	$('.js-agree').change(function () {
		let $this = $(this);
		let btn = $this.closest('.js-form').find('.submit');
		if ($this.is(':checked')) {
			btn.removeAttr("disabled");
		} else {
			btn.attr("disabled", true);
		}
	});


// Валидация формы
	let form = $('.js-form');

	form.each(function(){
		let thisForm = $(this);
		let url = thisForm.attr('action');

		thisForm.validate({
			focusInvalid: false,
			highlight: function(element) {
              $(element).closest(".form-field").addClass('form-field--error');
            },
            unhighlight: function(element) {
              $(element).closest(".form-field").removeClass('form-field--error');
            },
			submitHandler: function(form, event){
				event.preventDefault();
					let data = new FormData(),
						formParams = thisForm.serializeArray();

					$.each(thisForm.find('.js-input-file'), function(i, tag) {
						$.each($(tag)[0].files, function(i, file) {
							data.append(tag.name, file);
						});
					});

					$.each(formParams, function(i, val) {
						data.append(val.name, val.value);
					});

					data.append('key', 'value');

				/* это нужно будет вывеcти после успешной отправки формы,
				это здесь только для демонстарации выхода попапа */

					thisForm.find('button').prop('disabled', false);
					thisForm[0].reset();

					$('.popup').removeClass('is-show');

					let $select2 = $('.js-select');
					if ($select2.length) {
						$select2.each(function () {
							$(this).val('').trigger('change');
						})
					}
					//окно благодарности
					if (typeof thisForm.data('thank') !== 'undefined' ) {
						let idThank = thisForm.data('thank');
						$(idThank).addClass('is-show');
					}
				/* ******* */
				$.ajax({
					url: thisForm[0].action,
					type: 'POST',
					processData: false,
					contentType: false,
					data: data,
					beforeSend: function (data) {

						thisForm.find('button').attr('disabled', 'disabled');

					},
					success: function (data) {

						if (data['error']) {
							console.log('error');
						} else {
							setTimeout(function () {
								thisForm.find('button').prop('disabled', false);
								thisForm[0].reset();

								$('.popup').removeClass('is-show');

								let $select2 = $('.js-select');
								if ($select2.length) {
									$select2.each(function () {
										$(this).val('').trigger('change');
									})
								}
								//окно благодарности
								if (typeof thisForm.data('thank') !== 'undefined' ) {
									let idThank = thisForm.data('thank');
									$(idThank).addClass('is-show');
								}

							}, 500);
						}
					},
					error: function (xhr, ajaxOptions, thrownError) {
						console.log(xhr.status);
						console.log(thrownError);
					}
				});
				return false;
			}
		});

	});

	$B.on('click', 'label.error',function(){
	    $(this).hide().siblings('input').focus();

	});

	$B.on('keyup', 'input',function(){
	    $(this).siblings('label.error').hide();
	});

	$('.popup-thank').on('click', function(){
		$(this).removeClass('is-show');
	});

});

// Активация кнопки входа если все ок

$('.js-form-card').on('keyup',function(){
	let $this = $(this),
		val = $this.val();

	if(val.length >= 4){
		$('.js-active-btn').removeAttr("disabled");
	}else {
		$('.js-active-btn').attr("disabled", true);
	}
});

$('.js-form-emaillVal').on('keyup',function(){
	let $this = $(this),
		val = $this.val().replace(/[^0-9]/gim,'');
	if( val.length === 11 && val.length>1){
		$('.js-form-field').removeClass('form-entrance--inactive');
		$('.js-form-field').addClass('form-entrance--active');
		$('.js-btn--send').removeAttr("disabled");
		sendPhone(val)
	}
	else if(val.length<=1){
		$('.js-form-field').removeClass('form-entrance--inactive');
		$('.js-form-field').removeClass('form-entrance--active');
	}
	else {
		$('.js-form-field').removeClass('form-entrance--active');
		$('.js-form-field').addClass('form-entrance--inactive');
		$('.js-form-card').attr("disabled",true);
	}

});

function sendPhone(val){
console.log(val)
$('.js-btn--send').on('click', function() {

	if (val === "77777777777"){
		$('.js-form-description').show();
		$('.js-form-description-inval').hide();
		$('.js-form-card').removeAttr("disabled");
	}else{
		$('.js-form-field').removeClass('form-entrance--active');
		$('.js-form-description').hide();
		$('.js-form-description-inval').show();
		$('.js-form-card').attr("disabled",true);
	}

});
}
