//mobile tabs
	let $tabName = $('.js-tab__mobile .js-tabs__input');
	let $tabContent = $('.js-tab__content');

	$tabName.on('change', function(){
		if ($(this).hasClass('active')) {
            $(this).removeClass("active");
            $(this).closest('.js-tab__mobile').siblings($tabContent).slideUp(100)
        } else {
            $tabName.removeClass("active");
            $(this).addClass("active");
            $($tabContent).slideUp(200);
            $(this).closest('.js-tab__mobile').siblings($tabContent).slideDown(200)
        }
	});