//masonry-layout
	function gridMasonry(){
		const $grid = $('.js-grid');

		if ($grid.length) {
			$grid.each(function () {
				const gridWrap = document.querySelector('.js-grid');

				let msnry = new Masonry( gridWrap, {
					itemSelector: '.js-grid-item'
				});
			})
		}
	}



	$W.on('load',function () {
		gridMasonry();
	});



// Returns a function, that, as long as it continues to be invoked, will not
// be triggered. The function will be called after it stops being called for
// N milliseconds. If `immediate` is passed, trigger the function on the
// leading edge, instead of the trailing.

	function debounce(func, wait, immediate) {
		var timeout;
		return function() {
			var context = this, args = arguments;
			clearTimeout(timeout);
			timeout = setTimeout(function() {
				timeout = null;
				if (!immediate) func.apply(context, args);
			}, wait);
			if (immediate && !timeout) func.apply(context, args);
		};
	}


	let recountItems = debounce(function ($formFilter) {

		let data = new FormData(),
			formParams = $formFilter.serializeArray();

		$.each(formParams, function(i, val) {
			data.append(val.name, val.value);
		});
		data.append('key', 'value');

		$.ajax({
			cache: false,
			url: $formFilter.attr('action'),
			type: 'POST',
			method: $formFilter.attr('method'),
			processData: false,
			contentType: false,
			data: data,
			success: function(data) {
				if ($(data).length !== 0) {
					// let $elements = $(data);

					// $('.js-ajax-content').html($('.calendar-grid-item.js-grid-item', $elements).hide().fadeIn(1000));

					$('.js-ajax-content').html($(data).hide().fadeIn(1000));

					setTimeout(function () {
						gridMasonry();
					}, 250);
				}
			}
		}, 250);
	});


	let $elFilter = $('.js-filter-input');

	$elFilter.on('change', function() {
		let $this = $(this),
			$formFilter = $this.closest('.js-form-filter')

		recountItems($formFilter);
	});



$(function () {

//calendar tabs

//calendar tabs desctop
	let $tabItem = $('.js-tab__calendar-item .js-tabs__input');
	let $tabContainer = $('.js-tab-calendar');
	let $tabBlock = $('.js-tab__calendar-block');

	$tabItem.on('change', function(event) {
		let $this = $(this);
		let $item = $this.closest('.js-tab__calendar-item');
		let $unit = $this.closest($tabContainer);
		$unit.find($tabBlock).hide().eq($item.index()).fadeIn(200);
	});

//calendar tabs mobile
	let $tabName = $('.js-tab__calendar-mobile .js-tabs__input');
	let $tabContent = $('.js-tab__calendar-bowl');

	$tabName.on('change', function(){
		if ($(this).hasClass('active')) {
			$(this).removeClass("active");
			$(this).closest('.js-tab__calendar-mobile').siblings($tabContent).hide()
		} else {
			$tabName.removeClass("active");
			$(this).addClass("active");
			$($tabContent).hide();

			let $thisContent =  $(this).closest('.js-tab__calendar-mobile').siblings($tabContent);
			$thisContent.show();
		}
	});



//calendar select 
	let $month = $('.js-select-month'),
		$year = $('.js-select-year'),
		$inputMonth = $('.js-input-month'),
		$inputYear = $('.js-input-year'),
		$spanMonth = $('.js-span-month'),
		$btnCalendar = $('.js-calendar-btn'),
		$openCalendarSelect = $('.js-open-calendar-select');


	$openCalendarSelect.on('click', function(){
		let $unit = $(this).closest('.js-form-filter');

		$unit.find('.js-select-dropdown').fadeToggle();
	})


	$btnCalendar.on('click', function(event){
		event.preventDefault();
		let $unit = $(this).closest('.js-form-filter'),
			$nameMonth = $unit.find('.js-select-month option:selected').text(),
			$nameYear = $unit.find('.js-select-year option:selected').text(),
			$spanMonth = $unit.find('.js-span-month'),
			$spanYear = $unit.find('.js-span-year');

			$spanYear.text($nameYear);

			if ( $unit.find('.js-select-month').val() === 'all'){
				$('.calendar-select__month').text('');
			} else {
				$spanMonth.text($nameMonth);
			}

			$unit.find('.js-select-dropdown').fadeOut();

			$unit.find($elFilter).trigger('change');

	});

//open-close mobile filter 
	let $openFilter = $('.js-open-mobile-filter'),
		$closeFilter = $('.js-close-mobile-filter'),
		$mobileFilter = $('.js-mobile-filter')

	$openFilter.on('click', function(){
		$mobileFilter.addClass('active');
		$('.header').addClass('noactive');
	});

	$closeFilter.on('click', function(){
		$mobileFilter.removeClass('active');
		$('.header').removeClass('noactive');
	})

});