$(function () {

//main slider

// Today slider thumbs
	const speedSlide = 5000;

	let cartThumbs = new Swiper('.js-slider-category', {
		slidesPerView: 3,
		spaceBetween: 0,
		freeMode: true,
		autoplay: {
			delay: speedSlide
		},
		disableOnInteraction: false,
		speed: 0,
		watchSlidesProgress: true
	});

	// Today slider main (Parallax)
	  let cartMainSliderSelector = '.js-main-slider',
		  interleaveOffset = 0.5;

	  //count of today slider
	  let countSliderCart = $('.js-main-slider'),
		  itemSliderToday = $('.cart-slider__slide');

	  let cartMainSliderOptions = {
		loop: false,
		effect: 'fade',
		speed: 0,
		initialSlide: 0,
		grabCursor: false,
		simulateTouch: true,
		autoplay: {
			delay: speedSlide
		},
		disableOnInteraction: false,
		watchSlidesProgress: true,
		thumbs: {
		  swiper: cartThumbs
		},
		on: {
			init: function(){
				let $active = $('.js-slider-category .swiper-slide-thumb-active'),
				$activePrev = $active.prevAll();
				$('.js-slider-category .swiper-slide').removeClass('swiper-slide-line anim-line')
				$activePrev.addClass('swiper-slide-line');
				$active.addClass('anim-line')
			},
		  // progress: function () {
		  //   const isIE = navigator.appVersion.indexOf('Edge') > -1;
		  //   if (isIE) return;
		  //   let swiper = this;
		  //   for (let i = 0; i < swiper.slides.length; i++) {
		  //     let slideProgress = swiper.slides[i].progress,
		  //       innerOffset = swiper.width * interleaveOffset,
		  //       innerTranslate = slideProgress * innerOffset;
		  //     swiper.slides[i].querySelector('.slider-item__block').style.transform =
		  //       'translate3d(' + innerTranslate + 'px, 0, 0)';
		  //   }
		  // },
		  touchStart: function () {
			const isIE = navigator.appVersion.indexOf('Edge') > -1;
			if (isIE) return;
			let swiper = this;
			for (let i = 0; i < swiper.slides.length; i++) {
			  swiper.slides[i].style.transition = '';
			}
		  },
		  slideChangeTransitionStart: function(){
			let $active = $('.js-slider-category .swiper-slide-thumb-active'),
				$activePrev = $active.prevAll();
				$('.js-slider-category .swiper-slide').removeClass('swiper-slide-line anim-line')
				$activePrev.addClass('swiper-slide-line');
				$active.addClass('anim-line');

		  }
		  // setTransition: function (speed) {
		  //   const isIE = navigator.appVersion.indexOf('Edge') > -1;
		  //   if (isIE) return;
		  //   let swiper = this;
		  //   for (let i = 0; i < swiper.slides.length; i++) {
		  //     swiper.slides[i].style.transition = speed + 'ms';
		  //     swiper.slides[i].querySelector('.slider-item__block').style.transition =
		  //       speed + 'ms';
		  //   }
		  // }
		}
	  };

	  let todayMainSlider = new Swiper(cartMainSliderSelector, cartMainSliderOptions);

	  $(".slider-category__item").on('click', function(){
			todayMainSlider.autoplay.start();
		});


//video slider events
	let swiperServices = new Swiper('.js-slider-services', {
		slidesPerView: 1,
		spaceBetween: 20,
		loop: false,
		speed: 1200,
		watchSlidesVisibility: true,
		breakpoints: {
			480: {
				slidesPerView: 2
			},
			960: {
				slidesPerView: 3
			},
			1300: {
				slidesPerView: 4
			}
		}
	});

	$('.js-prev-services').on('click', function(){
		swiperServices.slidePrev(800);
	});

	$('.js-next-services').on('click', function(){
		swiperServices.slideNext(800);
	});


//instagram slider
	let instSwiper = new Swiper('.js-slider-instagarm', {
		slidesPerView: 1,
		spaceBetween: 12,
		loop: false,
		speed: 800,
		watchSlidesVisibility: true,
		simulateTouch: true,
		breakpoints: {
			481: {
				simulateTouch: true,
				spaceBetween: 20
			},
			961: {
				simulateTouch: false,
				spaceBetween: 0
			}
		}
	});

// Restaurants slider and placements
	let swipes = [];
	$('.js-slider-place').each(function(i, obj){
		swipes[i] = new Swiper(obj, {
		slidesPerView: 1,
		spaceBetween: 0,
		loop: true,
		speed: 1200,
		autoHeight: true,
		pagination: {
			el: '.swiper-pagination',
			clickable: true
		  },
		navigation: {
			nextEl: '.js-rest-next',
			prevEl: '.js-rest-prev'
		}
		});
	});


//rest slider
	if ($W.width() > 960) {
			let restSwiper = new Swiper('.js-rest-slider', {
				slidesPerView: 1,
				spaceBetween: 20,
				loop: false,
				speed: 800,
				effect: 'fade',
				simulateTouch: true,
				navigation: {
					nextEl: '.js-next-rest',
					prevEl: '.js-prev-rest',
				},
				breakpoints: {
					961: {
						spaceBetween: 0
					},
				}
			});
	} else {
		let restSwiper = new Swiper('.js-rest-slider', {
			slidesPerView: 1,
			spaceBetween: 20,
			loop: false,
			speed: 800,
			simulateTouch: true,
			navigation: {
				nextEl: '.js-next-rest',
				prevEl: '.js-prev-rest',
			},
			breakpoints: {
				961: {
					spaceBetween: 0
				},
			}
		});
	}

//events slider
	let eventsSwiper = new Swiper('.js-events-slider', {
		slidesPerView: 1,
		spaceBetween: 20,
		loop: false,
		speed: 800,
		simulateTouch: true,
		navigation: {
			nextEl: '.js-next-event',
			prevEl: '.js-prev-event',
		},
		breakpoints: {
			481: {
				spaceBetween: 30
			},
			961: {
				spaceBetween: 40
			},
			1211: {
				spaceBetween: 60
			},
		}
	});

// Parallax slider
	let gallerySelector = '.js-gallery-slider';
	let	interleaveOffsetGallery = 0.5;

	let galleryOptions = {
	  loop: false,
	  speed: 1500,
	  initialSlide: 0,
	  autoplay:{
		delay: 4000
	  },
	  grabCursor: false,
	  simulateTouch:false,
	  watchSlidesProgress: true,
	  navigation: {
		nextEl: '.js-next-gallery',
		prevEl: '.js-prev-gallery',
	  },
	  breakpoints: {
		700: {
		  simulateTouch: true
		}
	  },
	  on: {
		progress: function(){
		  let swiper = this;
		  for (let i = 0; i < swiper.slides.length; i++) {
			let slideProgress = swiper.slides[i].progress,
				innerOffset = swiper.width * interleaveOffsetGallery,
				innerTranslate = slideProgress * innerOffset;
				swiper.slides[i].querySelector(".js-gallery-slide").style.transform = "translate3d(" + innerTranslate + "px, 0, 0)";
		  }
		},
		touchStart: function() {
		  let swiper = this;
		  for (let i = 0; i < swiper.slides.length; i++) {
			swiper.slides[i].style.transition = "";
		  }
		},
		setTransition: function(speed) {
		  let swiper = this;
		  for (let i = 0; i < swiper.slides.length; i++) {
			swiper.slides[i].style.transition = speed + "ms";
			swiper.slides[i].querySelector(".js-gallery-slide").style.transition =
			  speed + "ms";
		  }
		}
	  }
	};
	let gallerySlider = new Swiper(gallerySelector, galleryOptions);


//relax slider
	let relaxSwiper = new Swiper('.js-slider-relax', {
		slidesPerView: 1,
		spaceBetween: 14,
		loop: false,
		speed: 800,
		simulateTouch: true,
		navigation: {
			nextEl: '.js-next-relax',
			prevEl: '.js-prev-relax',
		},
		breakpoints: {
			481: {
				spaceBetween: 30
			},
			961: {
				spaceBetween: 40,
				slidesPerView: 2
			},
			1211: {
				spaceBetween: 58,
				slidesPerView: 2
			},
		}
	});



//article slider
	let articleSwiper = new Swiper('.js-article-slider', {
		slidesPerView: 1,
		spaceBetween: 20,
		loop: false,
		speed: 800,
		simulateTouch: true,
		pagination: {
			el: '.swiper-pagination',
			clickable: true
		},
		navigation: {
			nextEl: '.js-next-article',
			prevEl: '.js-prev-article',
		},
		breakpoints: {
			600: {
				spaceBetween: 20,
				slidesPerView: 2
			},
			1211: {
				spaceBetween: 20,
				slidesPerView: 3
			},
		}
	});

// News slider
	let swiperNews = new Swiper('.js-slider-news', {
	    slidesPerView: 1,
	    spaceBetween: 20,
	    loop: false,
	    speed: 1200,
	    autoHeight: true,
	    watchSlidesVisibility: true,
	    breakpoints: {
	        480: {
	            slidesPerView: 2
	        },
	        960: {
	            slidesPerView: 3,
	            spaceBetween: 50
	        }
	    }
	});

	$('.js-prev-news').on('click', function(){
	    swiperNews.slidePrev(800);
	});

	$('.js-next-news').on('click', function(){
	    swiperNews.slideNext(800);
	});


});



