$(function () {

	let $openTour = $('.js-tour-icon');

	$openTour.on('click', function(){
		let $this = $(this),
			url = $this.data('src'),
			$unit = $this.closest('.js-tour-wrapper');

		console.log(url)
		$unit.find('.js-tour-iframe').toggleClass('active').attr('src', url);
		$unit.find('.js-slider-place').toggleClass('hide');
		$this.toggleClass('active');
	})
});