$(function () {
//fixed menu of tabs item
	let $subMenu = $('.js-sub-fixed')

	function subMenu(){
		let $offsetMenu = $('.js-offset-point').offset().top - 100;
		if ($W.scrollTop() > $offsetMenu) {
			$subMenu.addClass('fixed');
		} else{
			$subMenu.removeClass('fixed');
		}
	}

	if ($subMenu.length) {
		subMenu();
		$W.scroll(function(){
			subMenu();
		});
	}


	let $scrollto = $('.js-scrollto');


    let didScroll;
    let lastScrollTop = 0;
    let delta = 5;
    let navbarHeight = $('.header').outerHeight();

    let sections = $('.js-block-scroll'),
        nav = $('.js-scrollto__list'),
        nav_height = nav.outerHeight();




   $W.on('scroll touchmove', function(event) {
        didScroll = true;

        let cur_pos = $(this).scrollTop();
        let wh = $D.height();

        sections.each(function() {
            let top = $(this).offset().top - 136,
                bottom = top + $(this).outerHeight();

            if (cur_pos > top && cur_pos < bottom) {
                nav.find('a').removeClass('active');
                nav.find('a[href="#' + $(this).attr('id') + '"]').addClass('active');
            } else if(($W.scrollTop() * 100) / wh > 70) {
                nav.find('a').removeClass('active');
                nav.find('.js-scrollto__list li:last-child a').addClass('active');
            } else {
                nav.find('a[href="#' + $(this).attr('id') + '"]').removeClass('active');
            }
        });
    });

    $scrollto.on('click', function(e) {
        e.preventDefault();

        let elementClick = $(this).attr("href");
        let destination = $(elementClick).offset().top - 135;
        let destination2 = $(elementClick).offset().top -130;


        if ($W.width() > 960) {
            $("html:not(:animated),body:not(:animated)").animate({
                scrollTop: destination
            }, 1100);
        } else {
            $("html:not(:animated),body:not(:animated)").animate({
                scrollTop: destination2
            }, 1100);
        }
    });

});